@media only screen and (max-width: 890px){
	span.blog-author,div.post-heading>time,span.blog-shares{
		display: inline-block;
		float: none;
	}
	span.blog-author{
		max-width: 80vw;
	}
	span.blog-shares{
		margin-top: 0;
	}
	li>time{
		margin: 0;
	}
	.blog-content{
		font-size: 12pt;
		line-height: 1.2em;
		margin: 1em auto;
		padding: 1em;
		width: 87vw;
		border-radius: 10px;
	}
	#disqus_thread{
		max-width: 100%;
		padding: 15px;
	}
	.post-heading{
		padding: 0.5em;
		
	}
	
}