.carousel{
    /* width: 41%; */
    height: 0vw;
    margin: 0 auto;
    text-align: center;
    padding-top: 7%;
    padding-bottom: 25%;
}

.image-slide {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    height: 100%;
    width: 80%;
    margin-left: 10%;
    -webkit-transition: background-image .3s ease-in-out;
    /* transition: background-image .3s ease-in-out; */
    -webkit-transform: translateX(calc(- 20px));
            transform: translateX(calc(- 20px));
    -webkit-transition: -webkit-transform ease-out;
    transition: -webkit-transform ease-out;
    transition: transform ease-out ;
    transition: transform ease-out, -webkit-transform ease-out;
}

.slide-arrow {
	color: darkblue;
	cursor: pointer;
  font-size: 4rem;
  padding: 3%;
}

.slide-right {
  right: 1rem;
  float: right;
}

.slide-left {
  left: 1rem;
  float: left;
}

.slide-content{
  width: 20%;
  margin:1%;
}

.slide-content img{
  border-radius: 0%;
  margin-bottom: 10%;
  height: 250px;
  border-radius: 1%;
  width: 250px;
}

.slide-content img:hover{
 -webkit-transform : scale(1.1);
         transform : scale(1.1);
	box-shadow: 2px 8px 30px rgba(0, 0, 0, 0.25);
  }
  

