	margin-bottom: 0;
}

.newarrival-card .des-sp {
	display: inline-block;
	color: #000;
	font-size: 12px;
}

a {
	text-decoration: none;
}

.newarrival-card span {
	display: block;
    font-weight: 400;
    font-family: sans-serif;
    text-decoration: none;
    margin: 6px;
}

@media only screen and (max-width: 480px) {
	.newarrival-card {
		margin: 10px auto;
		min-width: 0;
		max-width: 100%;
		width: 75%;
		float: none;
	}
	#item-des{
		margin-left: 1em;
	}
	
	#item-res{
		margin-right: 2em;
	}
}
h1.blog-heading{
	text-align: center;
	vertical-align: middle;
	padding: 2em 0.5em;
	margin: 0 auto 0.5em auto;
	background: #13161C;
	font-size: 180%;
	color: azure;
	font-weight: 400;
	text-transform: capitalize;
	max-height: 30vh;
	text-overflow: ellipsis;
	overflow-y: scroll;
}

.post-heading{
	padding: 1.5em 0.25em;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin: auto;
	text-align: center;
	align-self: center;
	width: 90vw;
	box-shadow: 1px 3px 5px 1px #00000045;
	border-radius: 10px;
}

.blog-author{
	float: left;
	padding: 2px;
	margin-left: 1em;
	color: #979797;
	max-width: 26vw;
	overflow: hidden;
	text-overflow: ellipsis;
	word-wrap: none;
	height: 1.3em;
}
span.blog-author>span.name{
	color: #000;
	padding: 0;
	margin: 0;
	height: 1.3em;
	overflow: hidden;
	text-overflow: ellipsis;
}
.post-heading-ul{
	list-style: none;
}
li>time{
	margin-left: -10em;
	color: #979797;
	text-align: center;
	max-width: 25vw;
}
time>span{
	color: #000!important;
	text-align: center;
}
span.blog-shares{
	color: #979797;
	margin-right: 1em;
	float: right;
	margin-top: -1.25em;
}
/*span.blog-shares>i{
	color: #000;
	font-size: larger;
	margin: 0 0.5em;
}*/
span.blog-shares>a,span.blog-shares>a:visited{
	color: #000;
	font-size: larger;
	margin: 0 0.5em;
	outline: none;
}
p.text:first-of-type::first-letter{
	font-size: 250%;
	font-family: monospace;
}
.blog-content {
	font-size: 14pt;
	line-height: 1.4em;
	background: #fbf9f9;
	margin: 2em auto;
	margin-bottom: 0.5em;
	padding: 3em;
	width: 50vw;
	border: #c3c3c3 0.5px solid;
	border-radius: 10px;
	font-family: Arial, Helvetica, sans-serif;
}

.blog-content h2{
	color: #e43213;
}

.blog-content img{
	display: block;
	margin-left: auto;
	margin-right: auto;
	width: 75%;
}

#disqus_thread{
	margin-top: 4em;
	max-width: 50%;
	margin: 0 auto;
	margin-bottom: 2em;
}

