.events-page-container {
	width: 100%;
	margin: 0 auto;
	margin-top: 7vh;
	display: flex;
	flex-direction: column;
	position: relative;
	min-height: 70vh;
}

#conferences,
#meetups {
	margin-bottom: 10%;
	display: flex;
	flex-wrap: wrap;
}

.section-heading{
	margin-left: 2em;
	margin-top: 1em;
	padding: 15px;
}

#meetups hr {
	margin-bottom: 20px;
}

@media only screen and (max-width: 768px) {
	.events-page-container h1 {
		font-size: 1.5em;
	}
}

@media only screen and (max-width: 480px) {
	.events-page-container {
		width: 100%;
	}

	.events-page-container h1 {
		padding-left: 25px;
	}

	.section-heading{
		margin-left: 0px;
		text-align: left;
		padding: 15px;
	}
}